<!-- src/views/SupportResultApproval.vue -->
<template>
  <div class="priority-page">
    <h2 class="priority-title">ÏßÄÏõêÍ≤∞Í≥º ÏäπÏù∏ ÏöîÏ≤≠ Î™©Î°ù</h2>

    <!-- Í≤ÄÏÉâ / ÏÉÅÌÉú / Ï†ïÎ†¨ -->
    <div class="priority-filters">
      <input
        v-model="keyword"
        class="priority-input"
        placeholder="Ïù¥Î¶Ñ/Î≥¥Ìò∏Ïûê/Í∏∞Í¥Ä Í≤ÄÏÉâ"
        @keyup.enter="searchList"
      />

      <select v-model="state" class="priority-select" @change="searchList">
        <option value="">Ï†ÑÏ≤¥</option>
        <option value="BA1">ÏöîÏ≤≠</option>
        <option value="BA2">ÏäπÏù∏</option>
        <option value="BA3">Î∞òÎ†§</option>
      </select>

      <select v-model="orderBy" class="priority-select" @change="searchList">
        <option value="latest">ÏµúÏã†Ïàú</option>
        <option value="oldest">Ïò§ÎûòÎêúÏàú</option>
        <option value="name">Ïù¥Î¶ÑÏàú</option>
        <option value="priority">Ïö∞ÏÑ†ÏàúÏúÑÏàú</option>
      </select>
    </div>

    <div class="priority-card">
      <!-- Î°úÎî© ÌëúÏãú -->
      <div v-if="loading" class="priority-loading">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>

      <!-- Î™©Î°ù ÌÖåÏù¥Î∏î -->
      <table v-else class="priority-table">
        <thead>
          <tr>
            <th>ÏäπÏù∏ÏΩîÎìú</th>
            <th>Ïù¥Î¶Ñ</th>
            <th>Ïû•Ïï†Ïú†Ìòï</th>
            <th>Î≥¥Ìò∏Ïûê</th>
            <th>Îã¥ÎãπÏûê</th>
            <th v-if="isOrgVisible">Í∏∞Í¥Ä</th>
            <th>Í≤∞Í≥º ÏûëÏÑ±Ïùº</th>
            <th>Ïö∞ÏÑ†ÏàúÏúÑ</th>
            <th>ÏÉÅÌÉú</th>
            <th>Ï≤òÎ¶¨Ïùº</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in list"
            :key="item.approval_code"
            @click="goDetail(item)"
            class="priority-row"
          >
            <td>{{ item.approval_code }}</td>
            <td>{{ item.child_name || "Î≥∏Ïù∏" }}</td>
            <td>{{ item.disability_type }}</td>
            <td>{{ item.parent_name }}</td>
            <td>{{ item.manager_name }}</td>
            <td v-if="isOrgVisible">{{ item.org_name }}</td>
            <td>{{ formatDate(item.written_at) }}</td>

            <td>
              <span
                class="priority-chip"
                :class="priorityChipClass(item.priority_level)"
              >
                {{ codeLabel(item.priority_level) }}
              </span>
            </td>

            <td>
              <span class="priority-badge" :class="stateBadgeClass(item.state)">
                {{ codeLabel(item.state) }}
              </span>
            </td>

            <td>
              {{
                item.state === "BA1" || !item.approval_date
                  ? "-"
                  : formatDate(item.approval_date)
              }}
            </td>
          </tr>

          <tr v-if="list.length === 0">
            <td class="priority-empty" :colspan="isOrgVisible ? 10 : 9">
              Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§.
            </td>
          </tr>
        </tbody>
      </table>

      <!-- ÌéòÏù¥Ïßï -->
      <div v-if="!loading && totalPages > 1" class="priority-pagination">
        <button
          class="priority-page-btn"
          :disabled="page === 1"
          @click="changePage(page - 1)"
        >
          Ïù¥Ï†Ñ
        </button>

        <span class="priority-page-info">
          {{ page }} / {{ totalPages }} (Ï¥ù {{ totalCount }}Í±¥)
        </span>

        <button
          class="priority-page-btn"
          :disabled="page === totalPages"
          @click="changePage(page + 1)"
        >
          Îã§Ïùå
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { useAuthStore } from "@/store/authLogin.js";

const router = useRouter();
const auth = useAuthStore();

const list = ref([]);

// ÌéòÏù¥ÏßÄ Í¥ÄÎ†® ÏÉÅÌÉú
const page = ref(1);
const pageSize = ref(10);
const totalCount = ref(0);
const loading = ref(false);

// Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ Ïàò Í≥ÑÏÇ∞
const totalPages = computed(() =>
  totalCount.value > 0 ? Math.ceil(totalCount.value / pageSize.value) : 1
);

// Í≤ÄÏÉâÏñ¥, ÏÉÅÌÉú, Ï†ïÎ†¨
const keyword = ref("");
const state = ref("");
const orderBy = ref("latest"); // ÏµúÏã†Ïàú Í∏∞Î≥∏

// Ïú†Ï†Ä Ïó≠Ìï† ÏΩîÎìú (AA3 / AA4 Îì±)
const userRole = computed(() => {
  return (
    auth.role || // Ïòà: 'AA3', 'AA4'
    (auth.user && auth.user.roleCode) ||
    ""
  );
});

// Î°úÍ∑∏Ïù∏ ÏïÑÏù¥Îîî (Í∏∞Í¥Ä ÌïÑÌÑ∞ Í∏∞Ï§Ä)
const loginId = computed(() => {
  return (
    auth.userId ||
    auth.loginId ||
    auth.id ||
    (auth.user && auth.user.userId) ||
    ""
  );
});

// Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Ïó¨Î∂Ä (AA3)
const isOrgManager = computed(() => userRole.value === "AA3");

// ÏãúÏä§ÌÖú Í¥ÄÎ¶¨Ïûê Ïó¨Î∂Ä (AA4)
const isSystemAdmin = computed(() => userRole.value === "AA4");

// Ïù¥ ÌéòÏù¥ÏßÄ Ï†ëÍ∑º Í∞ÄÎä• Ïó¨Î∂Ä (AA3 ÎòêÎäî AA4)
const canViewSupportResultPage = computed(
  () => isOrgManager.value || isSystemAdmin.value
);

// Í∏∞Í¥Ä Ïª¨Îüº ÌëúÏãú Ïó¨Î∂Ä (Í∏∞Í¥Ä Í¥ÄÎ¶¨ÏûêÎ©¥ Ïà®ÍπÄ)
const isOrgVisible = computed(() => !isOrgManager.value);

// ÏÉÅÏÑ∏ÌôîÎ©¥ role Í∞í (AA3 ‚Üí 3, AA4 ‚Üí 4)
const detailRole = computed(() => {
  return isSystemAdmin.value ? 4 : 3;
});

// Í≥µÌÜµÏΩîÎìú Îß§Ìïë
const CODE_LABEL_MAP = {
  // ÏöîÏ≤≠ ÏÉÅÌÉú(BA)
  BA1: "ÏöîÏ≤≠",
  BA2: "ÏäπÏù∏",
  BA3: "Î∞òÎ†§",

  // Ïö∞ÏÑ†ÏàúÏúÑ Ïú†Ìòï(BB)
  BB1: "Í∏¥Í∏â",
  BB2: "Ï§ëÏ†ê",
  BB3: "Ï§ÄÎπÑ",
};

function searchList() {
  page.value = 1; // Í≤ÄÏÉâÌïòÎ©¥ ÌéòÏù¥ÏßÄ Ï¥àÍ∏∞Ìôî
  loadList();
}

function codeLabel(code) {
  if (!code) return "";
  return CODE_LABEL_MAP[code] || code;
}

function priorityChipClass(level) {
  switch (level) {
    case "BB1":
      return "priority-chip-danger";
    case "BB2":
      return "priority-chip-warning";
    case "BB3":
      return "priority-chip-info";
    default:
      return "priority-chip-default";
  }
}

function stateBadgeClass(state) {
  switch (state) {
    case "BA1":
      return "priority-badge-request";
    case "BA2":
      return "priority-badge-approve";
    case "BA3":
      return "priority-badge-reject";
    default:
      return "priority-badge-default";
  }
}

function formatDate(value) {
  if (!value) return "";
  const s = String(value);
  return s.length >= 10 ? s.slice(0, 10) : s;
}

// üîπ ÏßÄÏõêÍ≤∞Í≥º ÏäπÏù∏ Î™©Î°ù Ìò∏Ï∂ú
async function loadList() {
  loading.value = true;
  try {
    const res = await axios.get("/api/approvals/support-result", {
      params: {
        page: page.value,
        size: pageSize.value,
        keyword: keyword.value,
        state: state.value,
        orderBy: orderBy.value,
        loginId: loginId.value, // üîπ Î°úÍ∑∏Ïù∏ ÏïÑÏù¥Îîî
        role: userRole.value, // üîπ Ïó≠Ìï† (AA3 / AA4)
      },
    });

    const payload = res.data?.data || {};
    const rows = Array.isArray(payload.rows) ? payload.rows : [];

    list.value = rows;
    totalCount.value = payload.totalCount ?? 0;
  } catch (err) {
    console.error("[SupportResultApproval] loadList error:", err);
    list.value = [];
    totalCount.value = 0;
  } finally {
    loading.value = false;
  }
}

// ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω
function changePage(nextPage) {
  if (nextPage < 1) return;
  if (nextPage > totalPages.value) return;
  if (nextPage === page.value) return;

  page.value = nextPage;
  loadList();
}

// ‚úÖ Í∞Å Ìñâ ÌÅ¥Î¶≠ Ïãú ÏßÄÏõêÍ≤∞Í≥º ÏÉÅÏÑ∏Î°ú Ïù¥Îèô
function goDetail(item) {
  router.push({
    name: "resultDetail",
    params: { resultCode: item.result_code },
    query: {
      role: detailRole.value, // AA3 -> 3, AA4 -> 4
      submitCode: item.submit_code,
      planCode: item.plan_code,
    },
  });
}

onMounted(() => {
  if (!canViewSupportResultPage.value) {
    alert("Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Î∞è ÏãúÏä§ÌÖú Í¥ÄÎ¶¨ÏûêÎßå Ï†ëÍ∑ºÌï† Ïàò ÏûàÏäµÎãàÎã§.");
    router.push("/");
    return;
  }

  loadList();
});
</script>

<style scoped>
* {
  font-size: 15px;
}

.priority-page {
  max-width: 1600px;
  margin: 24px auto 40px;
  padding: 0 16px;
}

.priority-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #111827;
}

.priority-card {
  background: #ffffff;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 4px rgba(15, 23, 42, 0.06);
  padding: 12px 16px;
}

/* üîπ ÌÖåÏù¥Î∏î Í∏∞Î≥∏ Ïä§ÌÉÄÏùº (ÌõÑÏõê Í≤∞Í≥ºÏôÄ ÎèôÏùº Í≥ÑÏó¥) */
.priority-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
}

.priority-table thead th {
  text-align: center;
  padding: 10px 8px;
  font-weight: 600;
  font-size: 12px;
  color: #6b7280;
  border-bottom: 1px solid #e5e7eb;
  background: #f9fafb;
  white-space: nowrap;
}

.priority-table tbody td {
  padding: 9px 8px;
  border-bottom: 1px solid #f3f4f6;
  color: #374151;
  vertical-align: middle;
  text-align: center;
}

/* üîπ Ìñâ Ìò∏Î≤Ñ Ìö®Í≥º */
.priority-row {
  cursor: pointer;
  transition:
    background-color 0.12s ease,
    transform 0.06s ease;
}

.priority-row:hover {
  background: #f3f4ff;
  transform: translateY(-1px);
}

.priority-empty {
  text-align: center;
  padding: 14px 0;
  color: #9ca3af;
}

/* üîπ Ïö∞ÏÑ†ÏàúÏúÑ Ïπ© */
.priority-chip {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
}

.priority-chip-danger {
  background: #fee2e2;
  color: #b91c1c;
}

.priority-chip-warning {
  background: #fef3c7;
  color: #92400e;
}

.priority-chip-info {
  background: #e0f2fe;
  color: #075985;
}

.priority-chip-default {
  background: #e5e7eb;
  color: #4b5563;
}

/* üîπ ÏÉÅÌÉú Î∞∞ÏßÄ (ÏöîÏ≤≠/ÏäπÏù∏/Î∞òÎ†§) */
.priority-badge {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 999px;
  font-size: 11px;
  font-weight: 500;
  border: 1px solid transparent;
}

.priority-badge-request {
  background: #eef2ff;
  border-color: #c7d2fe;
  color: #3730a3;
}

.priority-badge-approve {
  background: #ecfdf5;
  border-color: #bbf7d0;
  color: #166534;
}

.priority-badge-reject {
  background: #fef2f2;
  border-color: #fecaca;
  color: #b91c1c;
}

.priority-badge-default {
  background: #f3f4f6;
  border-color: #e5e7eb;
  color: #4b5563;
}

/* üîπ Î°úÎî© ÌÖçÏä§Ìä∏ */
.priority-loading {
  font-size: 13px;
  color: #6b7280;
  padding: 8px 4px;
}

/* üîπ ÌéòÏù¥Ïßï ÏòÅÏó≠ */
.priority-pagination {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  color: #4b5563;
}

.priority-page-btn {
  padding: 4px 10px;
  border-radius: 999px;
  border: 1px solid #d1d5db;
  background: #f9fafb;
  cursor: pointer;
  font-size: 12px;
  transition:
    background-color 0.12s ease,
    transform 0.06s ease,
    box-shadow 0.12s ease;
}

.priority-page-btn:hover:not(:disabled) {
  background: #eef2ff;
  box-shadow: 0 1px 3px rgba(15, 23, 42, 0.08);
  transform: translateY(-0.5px);
}

.priority-page-btn:disabled {
  opacity: 0.5;
  cursor: default;
}

.priority-page-info {
  min-width: 120px;
  text-align: right;
}

/* üîπ ÏÉÅÎã® ÌïÑÌÑ∞ ÏòÅÏó≠ */
.priority-filters {
  display: flex;
  gap: 10px;
  margin-bottom: 12px;
  align-items: center;
}

.priority-input {
  flex: 1;
  padding: 6px 8px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 12px;
}

.priority-select {
  padding: 6px 8px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 12px;
  background: white;
}
</style>
